<template>
    <div>
        <canvas ref="draw" class="panel"></canvas>
        <ul>
            <li>
                <van-button type="primary" size="small" @click="drawHandler">画图</van-button>
                <!-- <button @click="drawHandler">画图</button> -->
            </li>
            <li>
                <van-button type="primary" size="small" @click="selectHandler">选择</van-button>
            </li>
            <li>
                <van-button type="primary" size="small" @click="translationHandler">平移</van-button>
            </li>
            <li>
                <van-button type="primary" size="small" @click="resetHandler">重置</van-button>
            </li>
            <li>
                <van-button type="danger" size="small" @click="clearHandler">清除</van-button>
            </li>
            <li>
                <van-button type="primary" size="small" @click="saveHandler">保存</van-button>
            </li>
            <li>
                <van-button size="small" plain @click="selectFileHandler">照片1</van-button>
                <input id="fileInput" type="file" accept="image/png,image/jpeg,image/jpg,image/bmp" style="display:none" v-on:change="fileChangeHandler">
            </li>
            <li>
            </li>
        </ul>

        <van-slider v-model="zoom" :min="0.25" :max="10" :step="0.01" :vertical="true" @update:model-value="sliderChangeHandler" />
    </div>
</template>
<script>
import { fabric } from "fabric";
export default {
    name: "BaseFabric",
    data() {
        return {
            canvas: null,
            zoom: 1,
            isDragging: false,
            lastPosX: 0,
            lastPosY: 0,
            viewportTransform: [],
        };
    },
    mounted() {
        const element = this.$refs.draw;
        const rect = element.parentElement.getBoundingClientRect();
        element.setAttribute("width", rect.width + "px");
        element.setAttribute("height", rect.height + "px");

        this.$nextTick(() => {
            this.init(element);
        });
        // this.reset();
        document.addEventListener("keydown", (event) => {
            const code = window.event ? event.keyCode : event.which;
            if (code == 8) {
                //删除选中
                this.canvas.remove(this.canvas.getActiveObject());
            }
        });
    },
    methods: {
        init(element) {
            let canvas = (this.canvas = new fabric.Canvas(element, {
                isDrawingMode: true,
            }));

            fabric.Object.prototype.transparentCorners = false;
            if (canvas.freeDrawingBrush) {
                canvas.freeDrawingBrush.color = "#ff0";
                canvas.freeDrawingBrush.width = 10;
                canvas.freeDrawingBrush.shadow = new fabric.Shadow({
                    blur: 0,
                    offsetX: 0,
                    offsetY: 0,
                    affectStroke: true,
                    color: "blue",
                });
            }

            console.log("canvas", canvas, this.$refs.draw);

            canvas.on("touch:gesture", (opt) => {
                console.log("touch:gesture", opt.e.type, opt.e.touches);
            });

            canvas.on("touch:drag", (opt) => {
                console.log("touch:drag", opt.e.type, opt.e.touches);
            });

            canvas.on("mouse:down", (opt) => {
                console.log("mouse:down", opt.e.type, opt);
                var evt = opt.e;
                if (evt.altKey === true && !this.canvas.isDrawingMode) {
                    this.isDragging = true;
                    this.canvas.selection = false;
                    this.lastPosX = evt.type == "touchstart" ? evt.targetTouches[0].clientX : evt.clientX;
                    this.lastPosY = evt.type == "touchstart" ? evt.targetTouches[0].clientY : evt.clientY;
                    console.log(this.lastPosX, this.lastPosY);
                }
            });

            canvas.on("mouse:move", (opt) => {
                console.log("mouse:move", opt.e.type, opt);
                if (this.isDragging) {
                    var evt = opt.e;
                    var vpt = this.canvas.viewportTransform;
                    const clientX = evt.type == "touchmove" ? evt.targetTouches[0].clientX : evt.clientX;
                    const clientY = evt.type == "touchmove" ? evt.targetTouches[0].clientY : evt.clientY;
                    vpt[4] += clientX - this.lastPosX;
                    vpt[5] += clientY - this.lastPosY;
                    console.log(clientX, this.lastPosX, clientY - this.lastPosY);
                    this.canvas.requestRenderAll();
                    this.lastPosX = clientX;
                    this.lastPosY = clientY;
                }
            });

            canvas.on("mouse:up", (opt) => {
                console.log("mouse:up", opt.e.type, opt);
                if (this.isDragging) {
                    // 鼠标向上移动时，我们希望重新计算所有对象的新交互，因此我们称之为setViewportTransform
                    this.canvas.setViewportTransform(this.canvas.viewportTransform);
                    this.isDragging = false;
                    this.canvas.selection = true;
                }
            });

            canvas.on("mouse:wheel", ({ e }) => {
                var delta = e.deltaY;
                var zoom = canvas.getZoom();
                zoom *= 0.999 ** delta;
                if (zoom > 10) zoom = 10;
                if (zoom < 0.25) zoom = 0.25;
                this.zoom = zoom;
                // canvas.setZoom(zoom);
                canvas.zoomToPoint({ x: e.offsetX, y: e.offsetY }, zoom);
                e.preventDefault();
                e.stopPropagation();

                var vpt = this.canvas.viewportTransform;
                if (zoom < 400 / 1000) {
                    vpt[4] = 200 - (1000 * zoom) / 2;
                    vpt[5] = 200 - (1000 * zoom) / 2;
                } else {
                    if (vpt[4] >= 0) {
                        vpt[4] = 0;
                    } else if (vpt[4] < this.canvas.getWidth() - 1000 * zoom) {
                        vpt[4] = this.canvas.getWidth() - 1000 * zoom;
                    }
                    if (vpt[5] >= 0) {
                        vpt[5] = 0;
                    } else if (vpt[5] < this.canvas.getHeight() - 1000 * zoom) {
                        vpt[5] = this.canvas.getHeight() - 1000 * zoom;
                    }
                }
            });
        },
        sliderChangeHandler() {
            this.canvas.setZoom(this.zoom);
            this.canvas.requestRenderAll();
            if ("wx" in window) {
                window["wx"].showToast({
                    titile: "值" + this.zoom,
                });
            }
        },
        drawHandler() {
            if (this.canvas) {
                this.canvas.isDrawingMode = true;
            }
        },
        selectHandler() {
            if (this.canvas) {
                this.canvas.isDrawingMode = false;
                console.log("对象集合", this.canvas.getObjects());
            }
        },
        translationHandler() {
            this.translation = true;
        },
        resetHandler() {
            this.clearHandler();
            let source = this.$refs.draw;
            const width = source.offsetWidth;
            const height = source.offsetHeight;
            // add random objects
            for (var i = 15; i--; ) {
                var dim = fabric.util.getRandomInt(30, 60);
                var klass = ["image", "Rect", "Triangle", "Circle"][fabric.util.getRandomInt(0, 2)];
                var options = {
                    top: fabric.util.getRandomInt(0, width),
                    left: fabric.util.getRandomInt(0, height),
                    fill: "green",
                };

                if (klass === "Circle") {
                    options.radius = dim;
                    let circle = new fabric.Circle({
                        radius: dim,
                        fill: "blue",
                        originX: "center", //调整中心点的X轴坐标
                        originY: "center",
                    });

                    let text = new fabric.Text("F" + dim, {
                        //绘制文本
                        fontSize: dim,
                        fill: "#FFF",
                        originX: "center",
                        originY: "center",
                    });

                    this.canvas.add(new fabric.Group([circle, text], options));
                } else if (klass == "image") {
                    fabric.Image.fromURL("/assets/logo.png", (img) => {
                        img.scale(0.5).set({
                            left: fabric.util.getRandomInt(0, width / 2),
                            top: fabric.util.getRandomInt(0, height) / 2,
                            angle: -15,
                            clipTo: function (ctx) {
                                ctx.arc(0, 0, 300, 0, Math.PI * 2, true);
                            },
                        });

                        this.canvas.add(img).setActiveObject(img);
                    });
                } else {
                    options.width = dim;
                    options.height = dim;
                    this.canvas.add(new fabric[klass](options));
                }
            }
        },
        clearHandler() {
            this.canvas.clear();
        },
        selectFileHandler(event) {
            if ("wx" in window) {
                window["wx"].chooseImage({
                    count: 1,
                    success(res) {
                        console.log(res);
                        console.log(res.tempFilePaths);
                    },
                });
            } else {
                document.getElementById("fileInput").click(event);
            }
        },
        fileChangeHandler(event) {
            console.log("文件地址", event.target);
            console.log("this.files", event.target.files);
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                fabric.Image.fromURL(reader.result, (img) => {
                    img.scale(0.5).set({
                        left: 0,
                        top: 0,
                        angle: 0,
                        clipTo: function (ctx) {
                            ctx.arc(0, 0, 300, 0, Math.PI * 2, true);
                        },
                    });

                    this.canvas.add(img).setActiveObject(img);
                });
            });
            reader.readAsDataURL(event.target.files[0]);
        },
        saveHandler() {
            const dataURL = this.canvas.toDataURL({
                width: this.canvas.width,
                height: this.canvas.height,
                left: 0,
                top: 0,
                format: "png",
            });

            window.postMessage(dataURL, "*");
            if ("wx" in window) {
                // window["wx"].miniProgram.postMessage({ data: dataURL });
                // window["wx"].miniProgram.navigateBack();
                window["wx"].downloadImage({
                    url: dataURL,
                    type: "image",
                    success(res) {
                        console.log("文件下载完成", res);
                    },
                    fail: function (res) {
                        console.log("download fail", res);
                    },
                    complete: function (res) {
                        console.log("download complete", res);
                    },
                });
                console.log("已经向微信小程序发送消息", dataURL);
            } else {
                console.warn("微信插件未加载");
                const a = document.createElement("a");
                a.style.display = "none";
                a.href = dataURL;
                a.download = "canvas.png";
                document.body.appendChild(a);
                a.click();
                setTimeout(() => {
                    document.body.removeChild(a);
                }, 100);
            }
        },
    },
};
</script>
<style scoped>
.panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: orange;
}

ul {
    position: absolute;
    list-style: none;
    padding: 0;
    margin: 0;
    right: 20px;
    bottom: 100px;
}

ul li {
    margin: 10px 0;
}

.van-slider {
    position: fixed;
    top: 50px;
    right: 30px;
    height: 300px;
}
</style>
